



















































.index .card{margin:40px 0 0 0
}
.index .ingredients{margin:30px auto 0
}
.index .ingredients li{display:inline-block
}
.index .delete{position:absolute;top:5px;right:5px;cursor:pointer;color:#aaa;-webkit-transition:all 0.5s linear 0s;transition:all 0.5s linear 0s
}
.index .delete:hover{color:#000
}

.add-smoothie h3{margin-top:30px
}
.add-smoothie .card{max-width:500px;margin:30px auto 30px auto
}
.add-smoothie .delete{position:absolute;bottom:15px;right:5px;cursor:pointer;color:#aaa;-webkit-transition:all 0.5s linear 0s;transition:all 0.5s linear 0s
}
.add-smoothie .delete:hover{color:#000
}

.edit-smoothie h3{margin-top:30px
